import React, { forwardRef } from "react";
import { Form, Input, InputNumber, TreeSelect } from 'antd';
// import { Selector } from '@/components';

export default forwardRef((props, ref) => {
  const [form] = Form.useForm();
  const { treeData = [], data = {} } = props
  const formItemLayout = {
    labelCol: { span: 4 },
    wrapperCol: { span: 18 },
  };
  return (
    <Form {...formItemLayout} form={form} ref={ref} initialValues={data}>
      <Form.Item label="" name="pId">
        <TreeSelect treeData={treeData} />
      </Form.Item>
      <Form.Item label="" name="title" rules={[{ required: true, message: '', }]}>
        <Input />
      </Form.Item>
      <Form.Item label="" name="icon">
        <Input />
      </Form.Item>
      <Form.Item label="" name="url">
        <Input />
      </Form.Item>
      {/* <Form.Item label="" name="view">
        <Selector
          api="dictionaryOptions"
          params={{ _id: '6087f9ee36ce3c4e47e509db' }}
          titlepropname="key"
          valuepropname="value"
        />
      </Form.Item> */}
      <Form.Item label="" name="path">
        <Input />
      </Form.Item>
      <Form.Item label="" name="moduleName">
        <Input />
      </Form.Item>
      <Form.Item label="" name="sortIndex">
        <InputNumber />
      </Form.Item>
    </Form>
  );
})